.labContainer{
    margin-top: 10%;
    padding: 2% 5% 5% 2%;
    background: #000;
    color: #fff;
    justify-content: center;
    align-items: center;
    display: inline-table;
    border: 10px solid rgb(85, 28, 28);
    overflow: hidden;
}

a{
    color:#fff;
}

.live {
    animation: blinker 1s linear infinite;
  }

  @keyframes blinker {
    50% {
      opacity: 0;
    }
  }

  .project{
    margin-top: 4%;
    animation: float 6s ease-in-out infinite;
  }