.containerC{
    
 font-family: Montserrat, sans-serif;
 
 display: flex;
 align-items: center;
 justify-content: center;

 overflow: hidden;
 margin: 0;
}

.contactButton{
    animation: float2 6s ease-in-out infinite;
    background: chartreuse;
    color: rgb(61, 59, 59);
    border: 2px solid rgb(10, 107, 70);
    border-radius: 50px;
    padding: 0.3rem 1.2rem;
    margin-top: 1%;
    margin-bottom: 5%;
    font-weight: bolder;
    outline: none;
    cursor: none;
    position: relative;
    transition: 0.2s ease-in-out;
    letter-spacing: 2px;
    margin-left: 10px;
    font-family: "Lucida Console", "Courier New", monospace;
}

.contactButton:nth-child(2){
    animation: float2 3s ease-in-out infinite;
    background: chartreuse;
    color: rgb(61, 59, 59);
    border: 2px solid rgb(10, 107, 70);
    border-radius: 50px;
    padding: 0.3rem 1.2rem;
    margin-top: 1%;
    margin-bottom: 1%;
    font-weight: bolder;
    outline: none;
    cursor: none;
    position: relative;
    transition: 0.2s ease-in-out;
    letter-spacing: 2px;
    margin-left: 10px;
    font-family: "Lucida Console", "Courier New", monospace;
}

@keyframes float2 {
    0% {
      box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
      transform: rotate(45deg);
    }
    50% {
      box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
      transform: translatey(-30px);
    }
    60% {
        box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
        transform: translateY(0px);
      }
    75% {
      box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
      transform: rotate(-45deg);
    }
    100% {
        box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
        transform: translatey(40px);
      }
  }



.contactButton:hover{
    background: #219655;
    border: 2px solid #219655;
    color: #fff;
    transform: scaleX(1.15);
}
.contactButton:visited{
    background: #219655;
    border: 2px solid #219655;
    color: rgb(94, 84, 84);
    transform: scaleX(1.15);
}