    background: #ececec;
    color: #360929;
    border-radius: 15px;
    border: 4px solid #000;
    padding-top: 8px;
    display: flex;
    text-align: center;
    animation: float-box 6s ease-in-out infinite;
  }

  .home-button {
    height: 50px;
    left: 49%;
    margin-left: -90px;
    margin-top: -90px;
    overflow: hidden;
    position: absolute;
    top: 80%;
    width: 180px;
    background: #5c157c;
    color: #ebe0e8;
    border-radius: 15px;
    border: 4px solid #5c157c;
    padding-top: 8px;
    display: flex;
    text-align: center;
    z-index: 1;
    justify-content: center;
    display: flex;
    animation: float 6s ease-in-out infinite;
  }
  
  .rainbow-preloader {
    height: 150px;
    left: 50%;
    margin-left: -90px;
    margin-top: -90px;
    overflow: hidden;
    position: absolute;
    top: 50%;
    width: 180px;
  }
  .rainbow-preloader:before, .rainbow-preloader:after {
    background: linear-gradient(45deg, transparent 33.333%, #6b9903 33.333%, #6b9903 66.667%, transparent 66.667%), linear-gradient(-45deg, transparent 33.333%, #6b9903 33.333%, #6b9903 66.667%, transparent 66.667%);
    background-size: 6px 34px;
    bottom: 0;
    content: "";
    display: block;
    height: 12px;
    left: 0;
    position: absolute;
    width: 66px;
    z-index: 10;
  }
  .rainbow-preloader:after {
    left: auto;
    right: 0;
  }
  .rainbow-preloader .rainbow-stripe {
    border-width: 12px;
    border-style: solid;
    border-radius: 100%;
    position: absolute;
  }
  .rainbow-preloader .rainbow-stripe:before {
    border-radius: 100%;
  }
  .rainbow-preloader .shadow {
    background: rgba(0, 0, 0, 0.2);
    bottom: -1px;
    box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.5);
    height: 2px;
    left: 0;
    opacity: 0;
    position: absolute;
    width: 0;
    animation: shadow-in-out 5s infinite;
  }
  .rainbow-preloader .shadow + .shadow {
    left: 118px;
    animation-delay: 0.5s;
  }
  
  .sky {
    background: linear-gradient(#85a5cc 0, #d0e4f2 100%);
    box-shadow: 0 0 20px #d0e4f2;
    height: 44%;
    overflow: hidden;
    position: absolute;
    width: 100%;
  }
  .sky .cloud {
    background: #ebf5fc;
    border-radius: 200px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    height: 39px;
    margin-right: -102px;
    position: absolute;
    right: 0%;
    width: 102px;
  }
  .sky .cloud.variant-1 {
    top: 51%;
    transform: scale(0.2);
    animation: clouds-in-out 16s 0s infinite linear;
  }
  .sky .cloud.variant-2 {
    top: 4%;
    transform: scale(0.4);
    animation: clouds-in-out 17s 2s infinite linear;
  }
  .sky .cloud.variant-3 {
    top: 25%;
    transform: scale(0.6);
    animation: clouds-in-out 18s 4s infinite linear;
  }
  .sky .cloud.variant-4 {
    top: 26%;
    transform: scale(0.8);
    animation: clouds-in-out 19s 6s infinite linear;
  }
  .sky .cloud.variant-5 {
    top: 61%;
    transform: scale(1);
    animation: clouds-in-out 20s 8s infinite linear;
  }
  .sky .cloud:before, .sky .cloud:after {
    background: #ebf5fc;
    border-radius: 100px;
    content: "";
    height: 45px;
    left: 10px;
    position: absolute;
    top: -10px;
    width: 60px;
    -webkit-transform: rotate(30deg);
    -moz-transform: rotate(30deg);
    transform: rotate(30deg);
  }
  .sky .cloud:after {
    height: 60px;
    left: auto;
    right: 12px;
    top: -24px;
    width: 60px;
  }
  
  .ground {
    background: linear-gradient(#6e9e03 0, #4f7302 100%);
    height: 57%;
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
  }

  .welcome-alert{
    top: 5%;
    left: 20%;
    color: rgb(65, 79, 161);
    position: absolute;
    justify-content: center;
    display: flex;
    align-items: center;
    font-size: xx-small;
    animation: wlcm-alert infinite cubic-bezier(0.19, 1, 0.22, 1) 10s both;
  }

  @keyframes wlcm-alert{
    to{
      transform: scaleX(1.5);
    }
  }
  
  @keyframes rainbow-in-out {
    0% {
      -webkit-transform: rotate(180deg);
    }
    33% {
      -webkit-transform: rotate(360deg);
    }
    66% {
      -webkit-transform: rotate(360deg);
    }
    100% {
      -webkit-transform: rotate(540deg);
    }
  }
  @keyframes shadow-in-out {
    0% {
      opacity: 0;
      width: 0;
    }
    33% {
      opacity: 1;
      width: 60px;
    }
    66% {
      opacity: 1;
      width: 60px;
    }
    100% {
      opacity: 0;
      width: 0;
    }
  }
  @keyframes clouds-in-out {
    0% {
      right: 0%;
    }
    100% {
      right: 105%;
    }
  }

  @keyframes float {
    0% {
      box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
      transform: translatey(0px);
    }
    50% {
      box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
      transform: translatey(-20px);
    }
    100% {
      box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
      transform: translatey(0px);
    }
  }

  @keyframes float-box {
    0% {
      box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
      transform: translateX(-20px);
    }
    25% {
      box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
      transform: translateX(0px);
    }
    50% {
      box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
      transform: translateX(20px);
    }
    75% {
      box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
      transform: translateX(0px);
    }
    100% {
      box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
      transform: translateX(-20px);
    }
  }
  
