.container{
    
 font-family: Montserrat, sans-serif;
 
 display: flex;
 align-items: center;
 justify-content: center;

 overflow: hidden;
 margin: 0;
}

.name{
    margin-top: 10%;
    font-weight: 400;
    font-size: 35px;
    line-height: 1.8;
    color: #45a745;
    font-family: "Lucida Console", "Courier New", monospace;
}

.highlight {
    background-color: #4c417a;
    opacity: 0.7;
    padding: 1px;
    color: rgb(223, 226, 241);
  }

  .describe1{
    margin-top: 4%;
    font-weight: 400;
    font-size: 25px;
    line-height: 1.8;
    color: #94691a;
    font-family: "Lucida Console", "Courier New", monospace;
  }
  .describe2{
    margin-top: 4%;
    font-weight: 400;
    font-size: 25px;
    line-height: 1.8;
    color: #94691a;
    font-family: "Lucida Console", "Courier New", monospace;
  }

  .skill-highlight {
    background-color: rgb(59, 216, 59);
    opacity: 0.6;
    padding-left: 1%;
    padding-right: 1%;
    color: #000;
    justify-content: center;
  }
  .skillButton {
    background: chartreuse;
    color: rgb(61, 59, 59);
    border: 2px solid rgb(10, 107, 70);
    border-radius: 50px;
    padding: 0.3rem 1.2rem;
    margin-top: 1%;
    margin-bottom: 1%;
    font-weight: bolder;
    outline: none;
    cursor: none;
    position: relative;
    transition: 0.2s ease-in-out;
    letter-spacing: 2px;
    margin-left: 10px;
    font-family: "Lucida Console", "Courier New", monospace;
  }

  h3 {
    margin: 10px 0;
   }
    
   h6 {
    margin: 5px 0;
    text-transform: uppercase;
   }
    
   p {
    font-size: 14px;
    line-height: 21px;
   }
    
   .card-container {
    background-color: #231E39;
    border-radius: 25px;
    box-shadow: 10px 10px 15px rgba(0,0,0,0.35);
    color: #B3B8CD;
    padding: 30px 0 0;
    position: relative;
    width: 350px;
    max-width: 100%;
    text-align: center;
    margin: 20px 0;
    overflow: hidden;
    animation: float-box 6s ease-in-out infinite;
   }

   @media screen and (max-width: 766px){
    .card-container {
      background-color: #231E39;
      border-radius: 25px;
      box-shadow: 10px 10px 15px rgba(0,0,0,0.35);
      color: #B3B8CD;
      padding: 30px 0 0;
      position: relative;
      width: 350px;
      max-width: 100%;
      text-align: center;
      margin: 20px 0;
      overflow: hidden;
      animation: float 6s ease-in-out infinite;
     }
   }
    
   .card-container .pro {
    color: #231E39;
    background-color: #FEBB0B;
    border-radius: 3px;
    font-size: 14px;
    font-weight: bold;
    padding: 3px 7px;
    position: absolute;
    top: 30px;
    left: 30px;
   }
    
   .card-container img {
    border: 1px solid #C400D7;
    border-radius: 50%;
    padding: 7px;
    height: 150px;
    width: 150px;
   }
    
   button.primary {
    background-color: #C400D7;
    border: 1px solid #C400D7;
    border-radius: 3px;
    color: #fff;
    font-family: Montserrat, sans-serif;
    font-weight: 500;
    padding: 10px 25px;
   }
    
   button.primary.ghost {
    background-color: transparent;
    color: #C400D7;
   }
    
   .skills {
    
    background-color: #1F1A36;
    text-align: left;
    padding: 15px;
    margin-top: 30px;
   }
    
   .skills ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
   }
    
   .skills ul li {
    background: #1cf009;
    color: #290d0d;
    border: 1px solid #000;
    border-radius: 5px;
    display: inline-block;
    font-size: 12px;
    margin: 0 7px 7px 0;
    padding: 7px;
    font-weight: bolder;
   }